import { SheetsRegistry, GenerateClassName } from "jss"
import { createGenerateClassName, Theme } from "@material-ui/core"
import theme from "./theme"

export interface IPageContext {
  theme: Theme
  sheetsManager: Map<any, any>
  sheetsRegistry: SheetsRegistry
  generateClassName: GenerateClassName<any>
}

const createPageContext = (): IPageContext => {
  return {
    theme,
    sheetsManager: new Map(),
    sheetsRegistry: new SheetsRegistry(),
    generateClassName: createGenerateClassName()
  }
}

declare const process: {
  browser: boolean
}

let pageContext: IPageContext

export const getPageContext = (): IPageContext => {
  if (!process.browser) {
    return createPageContext()
  }

  if (!pageContext) {
    pageContext = createPageContext()
  }

  return pageContext
}
